export class Switch extends HTMLElement {
    connectedCallback() {
        const checked = this.hasAttribute('checked');
        const disabled = this.hasAttribute('disabled');
        const userClass = this.getAttribute('class') || '';

        // Create the switch element
        const switchElement = document.createElement('button');

        // Set attributes and classes
        switchElement.setAttribute('type', 'button');
        switchElement.setAttribute('role', 'switch');
        switchElement.setAttribute('aria-checked', checked);
        switchElement.setAttribute('data-state', checked ? 'checked' : 'unchecked');
        switchElement.setAttribute('value', checked ? 'on' : 'off');
        switchElement.className = userClass

        // Pass all attributes from the custom element to the button
        for (const attr of this.attributes) {
            if (
                attr.name !== 'class' &&
                attr.name !== 'checked' &&
                attr.name !== 'disabled'
            ) {
                switchElement.setAttribute(attr.name, attr.value);
            }
        }

        // Create and append the thumb element
        const thumbElement = document.createElement('span');
        thumbElement.className = 'pointer-events-none block h-5 w-5 rounded-full bg-background shadow-lg ring-0 transition-transform data-[state=checked]:translate-x-5 data-[state=unchecked]:translate-x-0';
        switchElement.appendChild(thumbElement);

        // Set disabled state if necessary
        if (disabled) {
            switchElement.setAttribute('aria-disabled', 'true');
            switchElement.disabled = true;
        }

        // Add event listeners
        switchElement.addEventListener('click', this.toggle.bind(this));
        switchElement.addEventListener('keydown', this.handleKeyDown.bind(this));

        // Store references
        this.switchBtn = switchElement;
        this.switchThumb = thumbElement;

        // Replace the custom element with the newly created switch
        this.replaceWith(switchElement);

        // Update thumb position
        this.updateThumbPosition();
    }

    toggle() {
        if (this.switchBtn.disabled) return;

        const isChecked = this.switchBtn.getAttribute('aria-checked') === 'true';
        this.switchBtn.setAttribute('aria-checked', !isChecked);
        this.switchBtn.setAttribute(
            'data-state',
            isChecked ? 'unchecked' : 'checked',
        );
        this.switchBtn.value = isChecked ? 'off' : 'on';
        this.updateThumbPosition();
        this.dispatchEvent(
            new CustomEvent('change', { detail: { checked: !isChecked } }),
        );
    }

    handleKeyDown(event) {
        if (event.key === ' ' || event.key === 'Enter') {
            event.preventDefault();
            this.toggle();
        }
    }

    updateThumbPosition() {
        const isChecked = this.switchBtn.getAttribute('aria-checked') === 'true';
        this.switchThumb.setAttribute('data-state', isChecked ? 'checked' : 'unchecked');
    }

    static get observedAttributes() {
        return ['checked', 'disabled'];
    }

    attributeChangedCallback(name, oldValue, newValue) {
        if (!this.switchBtn) return;

        if (name === 'checked') {
            const isChecked = newValue !== null;
            this.switchBtn.setAttribute('aria-checked', isChecked);
            this.switchBtn.setAttribute(
                'data-state',
                isChecked ? 'checked' : 'unchecked',
            );
            this.switchBtn.value = isChecked ? 'on' : 'off';
            this.updateThumbPosition();
        } else if (name === 'disabled') {
            const isDisabled = newValue !== null;
            this.switchBtn.setAttribute('aria-disabled', isDisabled);
            this.switchBtn.disabled = isDisabled;
        }
    }
}